import React, { Component } from 'react'
import {Form, Input, Button, message} from 'antd'

import { markTemplate } from '../../api/experimets'

class TemplateModal extends Component{
  constructor(props){
    super(props);
  }
  submit=()=>{
    console.log('')
    const { getFieldValue,validateFields } = this.props.form
    validateFields(err => {
      if(!err){
        let params = {
          templateName: getFieldValue('name'),
          templateTags: getFieldValue('tags'),
          templateDesc: getFieldValue('desc'),
          experimentId:this.props.experimentId
          }
        markTemplate(params).then((res)=>{
          const newRes=res.data.meta;
          if(newRes.code==='001'){
            message.success(newRes.message)
          }else{
            message.error(newRes.message)
          }
          //modal
          this.props.close();
          }).catch(err=>{
            console.log(err)
        })
      }
    });
  }
 
  //
  reset=()=>{
    this.props.form.resetFields();
  }

  render(){
    const { getFieldDecorator } = this.props.form;
    return(
      <Form labelAlign='left'> 
        <Form.Item label=''>
          {getFieldDecorator('name',{rules: [{ required: true, message: 'Please input!' }]})(<Input placeholder=""/>)} 
        </Form.Item>
        
        <Form.Item label=''
          name='tags'
        >
          {getFieldDecorator('tags', {initialValue:'',rules: [{ required: true, message: 'Please input!' }]})
          (<Input.TextArea 
            placeholder=' , '/>
            )}
        </Form.Item>

        <Form.Item label=''>
          {getFieldDecorator('desc',{initialValue:''})(<Input.TextArea placeholder="" />)} 
        </Form.Item>
        <Form.Item style={{textAlign:'center'}}>
          <Button style={{marginRight:'5px'}} type='primary'
            onClick={this.reset}
          >
            
          </Button>
          <Button style={{marginLeft:'5px'}} type='primary'
            onClick={this.submit}
          >
            
          </Button>
        </Form.Item>
        
      </Form>
    )
  }
}

export default Form.create()(TemplateModal);