import React, { Component, Fragment } from 'react';
import { Card, Form, Input, Menu, Select, Button, Modal, message, Radio, Spin, Upload, Icon, Row, Col } from 'antd';
// import { getSavedModelByPage } from '../../../../../../api/process';
import { withPropsAPI } from 'gg-editor';
// import { connect } from 'react-redux';

const { Item } = Form;

class ModelPanel extends Component{

  constructor(props){
    super(props);
    this.state = {
      visible: false,
      value: '',
      loading: false
    }
  }

  handleSubmit = (e) => {
    if (e && e.preventDefault) {
      e.preventDefault();
    }

    const { form, propsAPI } = this.props;
    const { getSelected, executeCommand, update } = propsAPI;

    setTimeout(() => {
      form.validateFieldsAndScroll((err, values) => {
        console.log(values);
        if (err) {
          return;
        }

        const item = window.ITEM;

        if (!item) {
          return;
        }

        console.log(item)

        executeCommand(() => {
          update(item, {
            ...values,
          });
        });
      });
    }, 0);
  };

  handleClick = () => {
    this.setState({
      visible: true
    });
    if(!this.state.list){
      // this.setState({loading: true})
      const params = {
        pageNum: 1,
        pageSize: 1000
      }
      // getSavedModelByPage(params).then(res=>{
      //   console.log(res);
      //   if(res){
      //     if(res.data.meta.code == '001'){
      //       this.setState({ 
      //         loading: false,
      //         list: res.data.data['Model List'] 
      //       })
      //     }else{
      //       this.setState({loading:false})
      //     }
      //   }
      // })
    }
  }

  handleOk = e => {
    const { propsAPI, saveDatasetId } = this.props;
    const { executeCommand, update } = propsAPI;

    const item = window.ITEM;

    if (!item) {
      return;
    }

    console.log(item)

    executeCommand(() => {
      update(item, {
        conf: { modelId: this.state.value }
      });
    });

    this.setState({
      visible: false,
    });
  };


  onChange = e => {
    console.log('radio checked', e.target.addr);
    this.setState({
      value: e.target.value,
      addr: e.target.addr
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
      loading:false
    });
  };
 
  render(){
    const list = this.state.list ? this.state.list : []
    const { getFieldDecorator } = this.props.form;
    const { nodeType, nodeRemarks, conf} = this.props.detail;
    const { TextArea } = Input;
    const { form } = this.props;
    const radioStyle = {
      display: 'block',
      height: '30px',
      lineHeight: '30px',
    };
    return(
      <Fragment>
      <Form onSubmit={this.handleSubmit} labelCol={{span:6}} wrapperCol={{span:16}}>
        <Item label="">
          {form.getFieldDecorator('nodeType', {
            initialValue: nodeType,
            rules:[{
              validator:  (rule, val, callback)=>{
                if(val.length > 12){
                  callback('12')
                }
                callback();
              }
            }]
          })
          (<Input onBlur={this.handleSubmit} />)}
        </Item>
        <Item label="">
          {form.getFieldDecorator('nodeRemarks', {
            initialValue: nodeRemarks,
          })
          (<TextArea onBlur={this.handleSubmit} rows={3}/>)}
        </Item>
        {/* <Menu selectedKeys={[this.props.configCurrentKey]} mode="horizontal">
          <Menu.Item key="configuration">
            
          </Menu.Item>
          {/* <Menu.Item key="description">
            
          </Menu.Item> 
        </Menu> */}
        <Card title='IO' bordered={false} bodyStyle={{background: '#fff'}}>
          <Item label="">
            <Button type="primary" onClick={()=>this.handleClick()} block></Button>
          </Item>
        </Card>
      </Form>
      <Modal
        title=""
        visible={this.state.visible}
        onOk={()=>this.handleOk()}
        onCancel={this.handleCancel}
        okButtonProps={{ loading: this.state.loadingButton }}
      >
        <p>label</p>
        <Spin spinning={this.state.loading}>
          <Radio.Group onChange={this.onChange} value={this.state.value}>
            {
              list.map(item=>{
                return(
                  <Radio 
                    style={radioStyle} 
                    value={item.id}
                    addr={item.modelFileAddr}
                  >
                    {item.modelName}
                  </Radio>
                )
              })
            }
          </Radio.Group>
        </Spin>
      </Modal>
      </Fragment>
    )
  }

}

// export default Form.create()(withPropsAPI(connect(null,null)(ModelPanel)))
export default Form.create()(withPropsAPI(ModelPanel))
