import * as constants from './constants';

const defaultState = {
  processData:'',
  processId: '',
  paramsData: {},
  conversationId: '',
  processState: 0,
  nodesId: {}
};

export default(state=defaultState, action) => {
  const newState = JSON.parse(JSON.stringify(state));
  switch(action.type) {
    case(constants.SAVE_PROCESS):
      return {...newState, processData:action.data}
    case(constants.SAVE_PROCESSID):
      return {...newState, processId:action.processId}
    case(constants.SAVE_PARAMS):
      let paramsData = {...newState.paramsData, [action.name]: action.param}
      return {...newState, paramsData}
    case(constants.SAVE_CONVERID):
      return {...newState, conversationId: action.conversationId}
    case constants.SAVE_NODESID :
      const { nodesId } = action;
      return { ...newState, nodesId };
    case constants.SAVE_RESULT_LIST :
      const { resultListData, isModel, processLogId } = action;
      return { ...newState, resultList:resultListData, isModel, processLogId };
    case constants.SAVE_PROCESS_STATE :
      const { processState } = action;
      return { ...newState, processState };
    default:
      return state;
  }
}