import React from "react";
import { withPropsAPI, Command } from "gg-editor";
import { connect } from 'react-redux';
import { message, Modal, Input, Form } from 'antd';
import { getSpecialNodes, getXML } from '../getXML';
import IconFont from '../../../../../common/IconFont';
import {createProcess} from '../../../../../api/process.js'
import { actionCreators } from '../../store';
import styles from '../../index.module.less';

@withPropsAPI @Form.create()
class Save extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      visible: false,
      buttonLoading: false,
    }
  }

  // componentDidMount(){
  //   const {saveProcessName, saveProject, saveProcessId} = this.props
  //   saveProcessName('');
  //   saveProject('','');
  //   saveProcessId('');
  // }

  // 
  handleClick = () => {
    const { propsAPI, saveProject, processState } = this.props;
    console.log('processState',processState)
    if(processState == 0){
      this.changeStateIcon();
      const obj = propsAPI.save();
      console.log('obj',obj);
      const { nodes, edges } = obj;
      let specialNodes;
      let xmlString;
      if(nodes) {
        //
        if(edges) {
          specialNodes = getSpecialNodes(obj);
          console.log(specialNodes)
          if(specialNodes.firstNode.length !== 1 || specialNodes.firstNode[0].label !== 'start'){
            message.error("")
          }else{
            xmlString = getXML(nodes,edges,specialNodes);
          }
          if(xmlString){
            saveProject( obj, xmlString );
            this.setState({
              visible: true,
            });
          }else{
            message.info('')
          }
        }else{
          message.info('')
        }
      }
      else {
        message.info('')
      }
    }else{
      message.error('')
    }
  };

  changeStateIcon = () => {
    const { propsAPI, saveNodesId } = this.props;
    const { executeCommand, update, getSelected, find } = propsAPI;
    const nodesId = {};
    console.log(propsAPI);
    const selectedNodes = getSelected();
    console.log(selectedNodes);
    for(let i=0;i<selectedNodes.length;i++){
      const item = selectedNodes[i];
      const label = item.model.label;
      const id = item.id;
      nodesId[label] = id;
      executeCommand(() => {
        console.log(item)
        update(item, {
          state_icon_url:'',
        });
      });
    }
    saveNodesId(nodesId);
  }

  //XMLGGEditor
  handleSaveXML = () => {
    const { GGEditorObj, saveProcessId, processId, processName, savedXmlString} = this.props;
    const { getFieldsValue} = this.props.form
    let params = getFieldsValue();
    var f=true;
    this.setState({ buttonLoading:true})
    for(let i in params){
      if(params[i]===undefined){
        message.warning('!');
        f=false;
        this.setState({ buttonLoading:false})
        break;
      }
    }
    if(f){
      params = {...params, ggeditorObjectString: GGEditorObj, workflowXml: savedXmlString}
      createProcess(params).then(res=>{
        this.setState({ buttonLoading:false})
        console.log(res)
      }).catch(err=>this.setState({ buttonLoading:false}))
    }
    // if(processName){
    //   let params = {}
    //   if(processId){
    //     params = { 
    //       ggeditorString,
    //       xmlContent,
    //       processName,
    //       processId
    //     }
    //   }else{
    //     params = { 
    //       ggeditorString,
    //       xmlContent,
    //       processName
    //     }
    //   }
    //   saveXmlString(params).then((res)=>{
    //     if(res){
    //       if(res.data.meta.code === '001'){
    //         if(res.data.data){
    //           saveProcessId(res.data.data.processId);
    //         }
    //         message.success('')
    //       }else{
    //         message.error(''); 
    //       }
    //     }else{
    //       message.error(''); 
    //     }
    //   }).catch(error=>{
    //     console.log(error)
    //   });
    // }else{
    //   message.info('')
    // }
  };

  handleCancel = e => {
    this.setState({
      visible: false,
      nameValue: ''
    });
  };


  handleChange = e => {
    this.setState({
      nameValue: e.target.value,
    });
  }

  render() {
    const {getFieldDecorator}=this.props.form;
    const config={
        rules:[{
            required:true,
            message:'Please input!'
        }]
    }
    return (
      <div>
        <Command name="selectAll">
          <div className={styles.com} title="" onClick={this.handleClick}>
            <IconFont type={`icon-baocun`}/><span></span>
          </div>
        </Command>
        <Modal
          title=""
          visible={this.state.visible}
          onOk={this.handleSaveXML}
          onCancel={this.handleCancel}
          confirmLoading={this.state.buttonLoading}
        >
          {/* <Input placeholder="" onChange={this.handleChange} value={this.state.nameValue} style={{marginTop:'8px'}}/> */}
          <Form labelCol={{span:6}} wrapperCol={{span:12}}>
            <Form.Item label=''>
              {
                  getFieldDecorator('workflowName',config)(<Input placeholder='' />)
              }
            </Form.Item>
            <Form.Item label=''>
              {
                  getFieldDecorator('tagString',config)(<Input placeholder='' />)
              }
            </Form.Item>
            <Form.Item label=''>
              {
                  getFieldDecorator('workflowDesc',config)(<Input.TextArea placeholder='' />)
              }
            </Form.Item>
          </Form>
        </Modal>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  GGEditorObj: state.editorToolbar.GGEditorObj,
  savedXmlString: state.editorToolbar.xmlString,
  processId: state.editorToolbar.processId,
  processName: state.editorToolbar.processName,
  processState: state.editorToolbar.processState
})


const mapDispatchToProps = dispatch => {
  return{
    saveProject(obj, xmlString){
      dispatch(actionCreators.saveProject(obj, xmlString));
    },
    saveProcessId(processId){
      dispatch(actionCreators.saveProcessId(processId))
    },
    saveNodesId(nodesId){
      dispatch(actionCreators.saveNodesId(nodesId));
    },
    // saveProcessName(nameValue){
    //   dispatch(actionCreators.saveProcessName(nameValue))
    // },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Save);