import React, {Component}from 'react';
import { connect } from 'react-redux';
import actionCreators  from '../store/actionCreators';
import { Divider, Menu, Dropdown, Icon, Modal, Button, DatePicker } from 'antd';
import moment from 'moment';
import { Toolbar, Command } from 'gg-editor';
import IconFont from '../../../common/IconFont';
import Save from './save';
import styles from '../../components/EditorToolbar/index.module.less';

class FlowToolbar extends Component {

  render() {
    return(
      <div className={styles.canvaHeader}>
        <div className={styles.canvaTitle}>
          <span className={styles.taskflow}>
              <span>
                { this.props.templateName || '' } &nbsp;&nbsp;&nbsp;
              </span>
          </span>
        </div>
        <Toolbar className={styles.toolbar}>
          <Save />
          <Save title=''/>
          <Divider type="vertical" className={styles.divider} />
          <Command name="copy">
            <div className={styles.com} title="" >
              <IconFont type={`icon-copy`}/><span></span>
            </div>
          </Command>
          <Command name="paste">
            <div className={styles.com} title="" >
              <IconFont type={`icon-paste`}/><span></span>
            </div>
          </Command>
          <Command name="delete">
            <div className={styles.com} title="" >
              <IconFont type={`icon-delete`}/><span></span>
            </div>
          </Command>
          <Divider type="vertical" className={styles.divider} />
          <Command name="zoomIn">
            <div className={styles.com} title="" >
              <IconFont type={`icon-zoom-in`}/><span></span>
            </div>
          </Command>
          <Command name="zoomOut">
            <div className={styles.com} title="" >
              <IconFont type={`icon-zoom-out`}/><span></span>
            </div>
          </Command>
        </Toolbar>
        <span className={styles.runningMode} title="">
            <IconFont type={`icon-repeat`}/>
        </span>
      </div>
        )
    }
};
const mapStateToProps=(state)=>({
  templateName:state.template.name
});
const mapDispatchToProps=(dispatch)=>({

})
export default connect(mapStateToProps,mapDispatchToProps)(FlowToolbar);
// export default FlowToolbar;
