import React, { Component } from 'react';
import {Button, Table, Divider, Badge, Radio, message,  Popconfirm} from 'antd'
import {getTrashProcessData, deleteProcess, restoreProcess} from '../../api/process'
import {getTrashExperimentsData, getTrashRunningData,
        restoreExperiment, deepDeleteExperiment, 
        deepDeleteExperimentRunning, restoreExperimentRunning } from '../../api/experimets'

class Trash extends Component{
  constructor(props){
    super(props);
    this.state={
      select:0,       //0-  1- 2-
      trashSelectedRowKeys:[],
      dataSource:[],
      page:1,
      total:1,
      loading:false
    }
  }
  
  componentDidMount(){
    this.getTrashProcessData()
  }
  //
  onChange=e=>{
    this.setState({
      select:e.target.value
    })
    if(e.target.value===0){
      this.getTrashProcessData()
    }else if(e.target.value===1){
      this.getTrashExperimentsData()
    }else{
      this.getTrashRunningData()
    }
  }
  //
  onSelectChange = (trashSelectedRowKeys) => {
    this.setState({ trashSelectedRowKeys });
  };

  //
  getTrashProcessData=()=>{
    this.setState({
      loading: true,
      trashSelectedRowKeys:[]
    })
    const params = {
      pageNum: this.state.page,
      pageSize: 10,
      isDeleted:'1',
      userId:'2'
    } 
    const {page} = this.state
    getTrashProcessData(params).then((res)=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data.WorkflowVOList.map((item,index)=>{
            return { ...item, index: index+1+(page-1)*10, key: item.id}
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total  
          })
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
    
  }
  //
  getTrashExperimentsData=()=>{
    this.setState({
      loading: true,
      trashSelectedRowKeys:[]
    })
    const params = {
      pageNum: this.state.page,
      pageSize: 10
    }
    const {page} = this.state
    getTrashExperimentsData(params).then((res)=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['ExperimentRunning List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*10, key: item.id}
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total 
          })
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
  }
  //
  getTrashRunningData=()=>{
    this.setState({
      loading: true,
      trashSelectedRowKeys:[]
    })
    const params = {
      pageNum: this.state.page,
      pageSize: 10
    }
    const {page} = this.state
    getTrashRunningData(params).then((res)=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['ExperimentRunning List'].map((item,index)=>{
            return { Workflowid:item.Workflowid,
                      ...item.ExperiemntRunning,
                     index: index+1+(page-1)*10,
                    key: item.ExperiemntRunning.id}
          })
          console.log(dataSource)
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total  
          })
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
  }
  
  //
  restore=(ids)=>{
    const {select}=this.state;
    if(select===0){
      let params={'workflowId':String(ids)}
      console.log(params)
      restoreProcess(params).then(res=>{
        this.showMessage(res)
        this.getTrashProcessData()
      }).catch(err=>{
        console.log(err)
      })
    }else if(select===1){
      let params={'experimentId':String(ids)}
      console.log(params)
      restoreExperiment(params).then(res=>{
        this.showMessage(res)
        this.getTrashExperimentsData()
      }).catch(err=>{
        console.log(err)
      })
    }else{
      let params={'runningId':String(ids)}
      console.log(params)
      restoreExperimentRunning(params).then(res=>{
        this.showMessage(res)
        this.getTrashRunningData()
      }).catch(err=>{
        console.log(err)
      })
    }
  }
  //message
  showMessage=(res)=>{
    if(res){
      if(res.data.meta.code == '001'){
        message.success(res.data.meta.message)
      }else{
        message.error(res.data.meta.message)
      }
    }
  }
  //
  deepDelete=(ids)=>{
    const {select}=this.state;
    if(select===0){
      let params={'workflowId':String(ids)}
      console.log(params)
      deleteProcess(params).then(res=>{
        this.showMessage(res)
        this.getTrashProcessData()
      }).catch(err=>{
        console.log(err)
      })
    }else if(select===1){
      let params={'experimentId':String(ids)}
      console.log(params)
      deepDeleteExperiment(params).then(res=>{
        this.showMessage(res)
        this.getTrashExperimentsData()
      }).catch(err=>{
        console.log(err)
      })
    }else{
      let params={'runningId':String(ids)}
      console.log(params)
      deepDeleteExperimentRunning(params).then(res=>{
        this.showMessage(res)
        this.getTrashRunningData()
      }).catch(err=>{
        console.log(err)
      })
    }
  }

  render(){
    const {dataSource,select,trashSelectedRowKeys}=this.state;
    //id
    const trashProcessColumns = [
      {title: 'id',dataIndex: 'id',key: 'id'},
      {title: '', dataIndex: 'name',key:'name'},
      {title: '',dataIndex: 'action',
        render:(text,record)=>(
          <>
          <a onClick={()=>this.restore(record.id)}></a>
          <Divider type='vertical'/>
          <a onClick={()=>this.deepDelete(record.id)}></a>
          </>
        )},
    ];
     //
     const trashExperimentsColumns = [
      {title: 'id',dataIndex: 'id',key:'id'},
      {title: '', dataIndex: 'name',key:'name'},
      {title: 'id', dataIndex: 'fkWorkflowId',key:'fkWorkflowId'},
      {title: '',dataIndex: 'action',
        render:(text,record)=>(
          <>
          <Popconfirm
            title=""
            onConfirm={()=>this.restore(record.id)}
            // onCancel={cancel}
            okText=""
            cancelText=""
          >
              <a></a>
          </Popconfirm>
          
          <Divider type='vertical'/>
          <a onClick={()=>this.deepDelete(record.id)}></a>
          </>
        )},
    ];
    //
    const trashRunningColumns = [
      {title: 'id',dataIndex: 'id',key:'id'},
      {title: 'id', dataIndex: 'fkExperimentId',key:'fkExperimentId'},
      {title: '', dataIndex: 'startTime', width:'80px',ellipsis: true,key:'startTime'},
      {
        title: '',key: 'runningStatus',dataIndex: 'runningStatus',width:'80px',
        render: (text,record) => (
          <span>
            <Badge status={text===0 ? 'processing' : text===1 ? 'success' : 'error'} />
            {text===0 ? '' : text===1 ? '' : ''}
          </span>
        ),
      },
      {title: '',dataIndex: 'action',width:'120px',
        render:(text,record)=>(
          <>
          <Popconfirm
            title=""
            onConfirm={()=>this.restore(record.id)}
            // onCancel={cancel}
            okText=""
            cancelText=""
          >
              <a></a>
          </Popconfirm>
          <Divider type='vertical'/>
          <a onClick={()=>this.deepDelete(record.id)}></a>
          </>
        )},
    ];
    return(
      <div>
        <div style={{ marginBottom: 16 }}>
          {/*  */}
          <Radio.Group onChange={this.onChange} value={this.state.select}>
            <Radio value={0}></Radio>
            <Radio value={1}></Radio>
            <Radio value={2}></Radio>
          </Radio.Group>
          
        </div>
        <Table 
            bordered size='small'
            columns={select===0?trashProcessColumns:select===1?trashExperimentsColumns:trashRunningColumns} 
            dataSource={dataSource} 
            // pagination={false}
            rowSelection={
              { selectedRowKeys:trashSelectedRowKeys,
                onChange: this.onSelectChange
              }
            }
          />
          <div style={{textAlign:'left'}}>
            <Button style={{marginRight:'5px'}} type='primary'
              onClick={()=>this.restore(trashSelectedRowKeys)}
            >
              
            </Button>
            <Button style={{marginLeft:'5px'}} type='primary'
              onClick={()=>this.deepDelete(trashSelectedRowKeys)}
            >
              
            </Button>
          </div>
        </div>
    )
  }
}

export default Trash;