import { Toolbar, Command, withPropsAPI } from 'gg-editor';
import React, { Component } from 'react';
import { Row, Col, PageHeader, Form, message } from 'antd';
import GGEditor, { Flow } from 'gg-editor';
import { withRouter } from 'react-router-dom';
import  {connect} from 'react-redux'

// import EditorMinimap from '../components/EditorMinimap';
// import { FlowContextMenu } from '../components/EditorContextMenu';
// import { FlowToolbar } from '../components/EditorToolbar';
// import { FlowItemPanel } from '../components/EditorItemPanel';
// import { FlowDetailPanel } from '../components/EditorDetailPanel';
import CustomNode from '../components/node/nodeTensorflow';
import StartNode from '../components/node/nodeStart';


import '../components/EditorDetailPanel/index.less';
import { actionCreators } from '../components/EditorToolbar/store';
import { actionCreator } from '../components/store';
import styles from './index.module.less'
import Run from './run.js'




@withRouter @Form.create()
class CreateExperiment extends React.Component {

  constructor(props){
    super(props);
    console.log('processData', this.props.processData)
    const processObj = this.props.processData && JSON.parse(this.props.processData)
    // const processObj = this.props.processData
    console.log(processObj);
    if(processObj){
      processObj.nodes.map(item=>{
        item.id = item.id.toString()
      });
      processObj.edges.map((item,index)=>{
        item.id = (item.id + index).toString();
        item.source = item.source.toString();
        item.target = item.target.toString();
      })
    }
    this.state = {
      processObj
    }
  }

  
  handleCancel = () => {
    this.setState({visible: false})
  }

  componentDidMount(){
    // this.getRunningStatus()
  }

  render() {
    return (<div className={styles.canvasContainer}>
        <GGEditor className={styles.editor}>
            <PageHeader
              className={styles.pageheader}
              title=""
              onBack={() => this.props.history.push('/index/process')}
            //   subTitle=""
              extra={[
                // <Button type="primary" onClick={()=>this.setState({visible: true})}></Button>,    
              ]}
          />
          <Row type="flex" className={styles.editorBd} style={{height:'100%'}}>
            <Col xxl={18} xl={19} lg={18} md={17} sm={16} xs={16} className={styles.editorContent}>
              <CustomNode />
              <StartNode />
              <Flow 
                className={styles.flow}
                data={this.state.processObj}
                />
              <Run />
            </Col>
            {/* <Col xxl={6} xl={5} lg={6} md={7} sm={8} xs={8} className={styles.editorSidebar}>
              <FlowDetailPanel/>
            </Col> */}
          </Row>
        </GGEditor>
   </div> );
  }
};

const mapStateToProps = (state) => {
    return {
      rightSideVis: state.editor.rightSideVis,
      lock: state.editor.lock,
      // currentNode: state.editor.currentNode,
      processData: state.process.processData,
      processId: state.process.processId,
      paramsData: state.process.paramsData,
      conversationId: state.process.conversationId
    }
  }
  
const mapDispatchToProps = (dispatch) => {
    return {
      saveProcessId(processId){
        dispatch(actionCreators.saveProcessId(processId))
      },
      saveComponentInfo(componentInfo){
        dispatch(actionCreator.saveComponentInfo(componentInfo))
      }
    }
  }
  
export default connect(mapStateToProps, mapDispatchToProps)(withPropsAPI(CreateExperiment));



