import Axios from '../utils/request';

//
export async function getModelList(params) {
  return Axios({
    url:'/model/getModelList',
    method: 'GET',
    params
  })
}
//
export async function createModel(params) {
  return Axios({
    url:'/model/createModel',
    method: 'POST',
    params
  })
}

//
export async function editorModel(params) {
  return Axios({
    url:'/model/editModel',
    method: 'POST',
    params
  })
}
//
export async function deleteModel(params) {
  return Axios({
    url:'/model/deleteModel',
    method: 'POST',
    params
  })
}

//
export async function getSearchData(params) {
  return Axios({
    url:'/model/searchModelByName',
    method: 'POST',
    params
  })
}

//
export async function getTrashModelData(params) {
  return Axios({
    url:'/model/getModelInTrash',
    method: 'POST',
    params
  })
}
//
export async function restoreModel(params) {
  return Axios({
    url:'/model/restoreModel',
    method: 'POST',
    params
  })
}
//
export async function deepDeleteModel(params) {
  return Axios({
    url:'/model/deleteModelPermanently',
    method: 'POST',
    params
  })
}