import { combineReducers } from 'redux';
import { reducer as editorReducer } from '../pages/components/store';
import { reducer as editorToolbarReducer } from '../pages/components/EditorToolbar/store';
import { reducer as processReducer} from '../pages/process/store'
import templateReducer from '../pages/template/store/reducer';

const reducer = combineReducers({
  editor: editorReducer,
  editorToolbar: editorToolbarReducer,
  process: processReducer,
  
  template:templateReducer
});

export default reducer;