import React from 'react'
import {Link,withRouter} from 'react-router-dom'
import {Menu} from 'antd'
import * as Icon from '@ant-design/icons'

//
@withRouter
class CustomMenu extends React.Component {
  state = {
    openKeys: [],
    selectedKeys: []
  }

  componentDidMount() {
    // 
    const pathname = this.props.location.pathname
    //
    const rank = pathname.split('/')
    switch (rank.length) {
      case 2 :  //
        this.setState({
          selectedKeys: [pathname]
        })
        break;
      case 5 : //subMenu
        this.setState({
          selectedKeys: [pathname],
          openKeys: [rank.slice(0, 3).join('/'), rank.slice(0, 4).join('/')]
        })
        break;
      default :
        this.setState({
          selectedKeys: [pathname],
          openKeys: [pathname.substr(0, pathname.lastIndexOf('/'))]
        })
    }
  }

  onOpenChange = (openKeys) => {
    //
    if (openKeys.length === 0 || openKeys.length === 1) {
      this.setState({
        openKeys
      })
      return
    }

    //
    const latestOpenKey = openKeys[openKeys.length - 1]
    //
    //key
    //3
    if (latestOpenKey.includes(openKeys[0])) {
      this.setState({
        openKeys
      })
    } else {
      this.setState({
        openKeys: [latestOpenKey]
      })
    }
  }

  renderMenuItem = ({key, icon, title,}) => {
    return (
      <Menu.Item key={key}>
        <Link to={key} style={{font: '64px'}}>
          {/* {icon && <Icon type={icon}/>} */}
          {icon &&  React.createElement(Icon[icon])}
          <span>{title}</span>
        </Link>
      </Menu.Item>
    )
  }
  renderSubMenu = ({key, icon, title, subs}) => {
    return (
      <Menu.SubMenu key={key} title={<span>{icon &&  React.createElement(Icon[icon])}<span>{title}</span></span>}>
        {
          subs && subs.map(item => {
            return item.subs && item.subs.length > 0 ? this.renderSubMenu(item) : this.renderMenuItem(item)
          })
        }
      </Menu.SubMenu>
    )
  }

  render() {
    const {openKeys, selectedKeys} = this.state
    return (
      <Menu
        onOpenChange={this.onOpenChange}
        theme='dark'
        onClick={({key}) => this.setState({selectedKeys: [key]})}
        openKeys={openKeys}
        selectedKeys={selectedKeys}
        mode='inline'>
        {
          this.props.menus && this.props.menus.map(item => {
            console.log(item)
            return item.subs && item.subs.length > 0 ? this.renderSubMenu(item) : this.renderMenuItem(item)
          })
        }
      </Menu>
      // <div></div>
    )
  }
}

export default CustomMenu