import React, { Component } from 'react';
import { PageHeader, Table, message, Spin, Divider, Button, Modal, Row, Col, Input, Form,
     Card, Empty, Tag, Select, Avatar, Icon} from 'antd';

import {getUploadDataset,getPublicDataset,deleteDataset,
  searchPublicDatasetByName,searchUserDatasetByName,searchPublicDatasetByTags,
    searchUserDatasetByTags,downloadDataset,previewDataset} from '../../api/dataset';
import UploadDataset from './UploadDataset';
import Trash from './trash'
import style from './index.module.less'

class  Dataset extends Component{

  constructor(props){
    super(props)
    this.state = {
      //
      dataSource: [],
      loading: false,
      visible: false,
      loadingButton: false,
      radioValue:'classic',
      previewVisible: false,
      previewColumns: [],
      previewData: [],
      previewPage:1,
      trashVisible:false,
      total:1,
      searchType:'name',
      page:1
    }
  }

  componentDidMount(){
    this.getData();
  }


  //
  getData = () => {
    this.setState({loading: true})
    const {page} = this.state
    const params = {
      pageNum:page,
      pageSize:7
    }
    getPublicDataset(params).then(res=>{
      console.log(res)
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Dataset List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*6 }
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total
          })
        }else{
          message.error('')
          this.setState({ loading: false })
        }
      }
    }).catch(err=>this.setState({ loading: false }))
  }

  //
  getUploadData = () => {
    this.setState({loading: true})
    const {page} = this.state
    const params = {
      pageNum: page,
      pageSize: 7,
      userId:2
    }
    getUploadDataset(params).then(res=>{
      console.log(res)
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Dataset List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*6}
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data['Total']
          })
        }else{
          message.error('')
          this.setState({ loading: false })
        }
      }
    }).catch(err=>this.setState({ loading: false }))
  }

  //
  handleDelect = (record) => {
    console.log(record)
    const params = {datasetId:record.id}
    deleteDataset(params).then(res=>{
      if(res){
        console.log(res)
        if(res.data.meta.code == '001'){
          message.success('')
          this.getUploadData()
        }else{
          message.error('')
        }
      }
    })
  }
  //
  radioChange = val => {
    this.setState({
      radioValue: val,
      search:'',
      page: 1
    },()=>{
      console.log(this.state.radioValue)
      const { radioValue } = this.state
      radioValue === 'user' && this.getUploadData()
      radioValue === 'classic' && this.getData()
    });
  };
  //modal
  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  //modal
  showModal = () => {
    this.setState({
      visible: true,
    });
  };
  

  //
  preview = record => {
    // console.log(record)
    this.setState({
      previewVisible: true,
      previewLoading: true
    })
    let previewColumns,previewData,previewDesc
    previewDataset({datasetId:record.id}).then(res=>{
      console.log(res)
      if(res){
        this.setState({previewLoading: false})
        if(res.data.meta.code === '001'){
          previewDesc = res.data.data.desc
          previewColumns = res.data.data.content[0].map(item =>{
            return {
              title: item,
              dataIndex: item,
              key: item
            }
          })
          previewData = res.data.data.content.map(item=>{
            let preview = {}
            for(let i=0;i<item.length;i++){
              preview = { ...preview, [previewColumns[i].title]:item[i] }
            }
            return preview
          })
          this.setState({
            previewColumns,
            previewData,
            previewDesc
          })
        }
      }
    })
  }
  //
  handlePreCancel = () => {
    this.setState({
      previewVisible: false,
      previewPage: 1,
      previewData:[],
      previewDesc:''
    })
  }
  //
  datasetPageChange = (page) => {
    console.log(page);
    this.setState({page},()=>{
      if(this.state.search){
        this.state.radioValue === 'user' && this.userSearch()
        this.state.radioValue === 'classic' && this.classicSearch()
      }else{
        this.state.radioValue === 'user' && this.getUploadData()
        this.state.radioValue === 'classic' && this.getData()
      }
    });
  }
  // 
  pageChange = (page) => {
    this.setState({
      previewPage: page
    })
  }


  //
  handleSearch = search => {
    const { radioValue } = this.state
    this.setState({
      loading:true,
      page:1,
      search
    }, function(){
      if(radioValue === 'classic'){
        this.classicSearch(search)
      }else if(radioValue === 'user'){
        this.userSearch(search)
      }
    })
  }

  classicSearch = search => {
    const {searchType}=this.state;
    const {page} = this.state
    const params = {
      pageNum: page,
      pageSize: 7,
    }
    if(searchType=='name'){
      params.datasetName=search
      searchPublicDatasetByName(params).then(res=>{
        console.log(res)
        if(res){
          if(res.data.meta.code == '001'){
            const dataSource = res.data.data['Dataset List'].map((item,index)=>{
              return { ...item, index: index+1+(page-1)*10 }
            })
            this.setState({ 
              dataSource, 
              loading: false,
              total: res.data.data.Total
            })
          }else{
            message.error('')
            this.setState({ loading: false })
          }
        }
      }).catch(err=>this.setState({ loading: false }))
    }else{
      params.datasetTags=search;
      searchPublicDatasetByTags(params).then(res=>{
        console.log(res)
        if(res){
          if(res.data.meta.code == '001'){
            const dataSource = res.data.data['Dataset List'].map((item,index)=>{
              return { ...item, index: index+1+(page-1)*10 }
            })
            this.setState({ 
              dataSource, 
              loading: false,
              total: res.data.data.Total
            })
          }else{
            message.error('')
            this.setState({ loading: false })
          }
        }
      }).catch(err=>this.setState({ loading: false }))
    }
  }

  userSearch = search => {
    const {searchType}=this.state;
    const {page} = this.state
    const params = {
      pageNum: page,
      pageSize: 7,
      userId:2
    }
    if(searchType=='name'){
      params.datasetName=search;
      searchUserDatasetByName(params).then(res=>{
        console.log(res)
        if(res){
          if(res.data.meta.code == '001'){
            const dataSource = res.data.data['Dataset List'].map((item,index)=>{
              return { ...item, index: index+1+(page-1)*10 }
            })
            this.setState({ 
              dataSource, 
              loading: false,
              total: res.data.data.Total
            })
          }else{
            message.error('')
            this.setState({ loading: false })
          }
        }
      }).catch(err=>this.setState({ loading: false }))
    }else{
      params.datasetTags=search;
      searchUserDatasetByTags(params).then(res=>{
        console.log(res)
        if(res){
          if(res.data.meta.code == '001'){
            const dataSource = res.data.data['Dataset List'].map((item,index)=>{
              return { ...item, index: index+1+(page-1)*10 }
            })
            this.setState({ 
              dataSource, 
              loading: false,
              total: res.data.data.Total
            })
          }else{
            message.error('')
            this.setState({ loading: false })
          }
        }
      }).catch(err=>this.setState({ loading: false }))
    }
  }

  onSearchChange = e =>{
    this.setState({
      search: e.target.value
    })
  }
  changeSearchType=(v)=>{
    this.setState({
      searchType:v
    })
  }
  downloadDataset=(record)=>{
    const params={datasetId:record.id};
    downloadDataset(params).then(res=>{
      console.log(res)
      if(res){
        var blob = new Blob([res.data.data])
        console.log(blob)
        var href = window.URL.createObjectURL(blob);
        var link = document.createElement('a');
        link.setAttribute("download", 'result.csv');
        link.href = href;
        link.click();
        message.success('!')
      }
    }).catch(err=>{})
  }

  render(){
    const { searchType,dataSource,previewColumns, previewData, previewDesc, total, radioValue, trashVisible} = this.state

    //
    const columns = [
      { align: 'center', render: ()=><Avatar size={48} icon={<Icon type="file-text" theme="filled" />} style={{ backgroundColor: '#27afff' }} />},
      // {title: '', dataIndex: 'index', key: 'index',},
      {title: '',dataIndex: 'name',key: 'name',},
      {title: '',dataIndex: 'tags', key: 'tags',
        render:tags=>(
          <>
            {tags.split(',').map((item,index)=>(
              <Tag color='orange' key={index}>{item}</Tag>
            ))}
          </>
        )},
      { title: '',dataIndex: 'createTime', key: 'createTime',},
      {title: '', dataIndex: 'type',key: 'type',
        render: (text, record) => <span>{ text===0 ? '' : text ===1 ? '' : '' }</span>
      },
      {title: '',dataIndex: 'datasetDesc', key: 'datasetDesc',},
      {title: '',key: 'action',
        render: (text, record) => (
          <span>
            <a onClick={()=>this.preview(record)}></a>
            <Divider type="vertical" />
            <a onClick={()=>this.downloadDataset(record)}></a>
            {this.state.radioValue === 'user' &&  <Divider type="vertical" />}
            {this.state.radioValue === 'user' &&  <a onClick={e=>this.handleDelect(record)}></a> }
          </span>
        )},
    ];

  return(
      <div className={style.contentWrap}>
        <PageHeader
          style={{border: '1px solid rgb(235, 237, 240)',marginBottom: '13px'}}
          title="" subTitle=""
          extra={[<Button type='primary' onClick={this.showModal}></Button>]}
        />
        <div className={style.buttonWrap}>
          <Button type={radioValue==='classic'?'primary':''} onClick={()=>this.radioChange('classic')}></Button>
          <Button type={radioValue==='classic'?'':'primary'} onClick={()=>this.radioChange('user')} style={{marginLeft:'10px'}}></Button>
          <Button type='primary' style={{float:'right'}} onClick={()=>{
                this.setState({trashVisible:true})
                this.trash ? this.trash.getDatasetData() : console.log('')
            }}></Button>
          
          <Input.Search 
              onSearch={value=>this.handleSearch(value)} 
              value={this.state.search}
              onChange={this.onSearchChange}
              placeholder=""
              style={{float:"right",width:360, marginRight:'10px'}}
              addonAfter={
                <Select value={searchType} onChange={this.changeSearchType} style={{float:"right",width:120}}>
                  <Select.Option value='tag'></Select.Option>
                  <Select.Option value='name'></Select.Option>
                </Select>
              }
          />
        </div>
        
        <Spin spinning={this.state.loading} tip='loading...'>
          <Table 
            columns={columns} 
            dataSource={dataSource} 
            pagination={{
              onChange:this.datasetPageChange,
              current:this.state.page,
              total,
              pageSize: 7,
            }}
          />
        </Spin>
        
        {/* modal */}
        <Modal
          title=""
          footer={null}
          visible={this.state.visible}
          onCancel={()=>{
            this.setState({visible: false})
            this.upload.clear();
            this.getUploadData()
        }}
        >
          <UploadDataset  close={()=>{
            this.setState({visible: false});
          }} onRef={ref=>this.upload=ref} getUploadData={this.getUploadData}/>
        </Modal>

        <Modal
          title=""
          visible={trashVisible}
          footer={null}
          onCancel={()=>{
            this.setState({trashVisible:false})
            radioValue=='classic'?this.getData():this.getUploadData();
          }}
        >
          <Trash onRef={ref=>this.trash=ref}/>
        </Modal>
        {/* copy */}
        <Modal
          title=""
          visible={this.state.previewVisible}
          onCancel={this.handlePreCancel}
          footer={null}
          width={'85%'}
        >
          <Spin tips='loading' spinning={this.state.previewLoading}>
            <Row>
              <Col span={17}>
                <Table 
                  columns={previewColumns} 
                  dataSource={previewData} 
                  size="small" 
                  showHeader={false}
                  pagination={{current:this.state.previewPage,onChange:this.pageChange}}
                />
              </Col>
              <Col span={6} offset={1}>
                <Card style={{maxHeight:'420px',overflow:'auto'}}>
                  <p>{previewDesc ? previewDesc: <Empty description='' /> }</p>
                </Card>
              </Col>
            </Row>
          </Spin>
        </Modal>
      </div>
  )
  }
}

export default Form.create()(Dataset);