import React from 'react';
import { Row, Col, Input, PageHeader, Button, Modal, Form, message } from 'antd';
import GGEditor, { Flow } from 'gg-editor';
import { withRouter } from 'react-router-dom';
import  {connect} from 'react-redux'

import EditorMinimap from '../components/EditorMinimap';
import { FlowContextMenu } from '../components/EditorContextMenu';
import { FlowToolbar } from '../components/EditorToolbar';
import { FlowItemPanel } from '../components/EditorItemPanel';
import { FlowDetailPanel } from '../components/EditorDetailPanel';
import CustomNode from '../components/node/nodeTensorflow';
import StartNode from '../components/node/nodeStart';
import IconFont from '../../common/IconFont';
import { NodePanel, DetailPanel } from 'gg-editor';
import DetailForm from '../components/EditorDetailPanel/DetailForm';
import '../components/EditorDetailPanel/index.less';
import { actionCreators } from '../components/EditorToolbar/store';
import { actionCreator } from '../components/store';
import styles from './index.module.less'
import {loadPublicComponentInfo} from '../../api/component'
import {createExperiment} from '../../api/experimets'

@withRouter @Form.create()
class CreateExperiment extends React.Component {

  constructor(props){
    super(props);
    console.log('processData', this.props.processData)
    const processObj = this.props.processData && JSON.parse(this.props.processData)
    // const processObj = this.props.processData
    console.log(processObj);
    if(processObj){
      processObj.nodes.map(item=>{
        item.id = item.id.toString()
      });
      processObj.edges.map((item,index)=>{
        item.id = (item.id + index).toString();
        item.source = item.source.toString();
        item.target = item.target.toString();
      })
    }
    this.state = {
      processObj
    }
  }

  // componentWillUnmount(){
  // }

  handleSaveExp = () => {  // 
    console.log(this.state.processObj)
    const { getFieldsValue} = this.props.form
    let params = getFieldsValue();
    let f = true
    for(let i in params){
      if(params[i]===undefined){
        message.warning('!');
        f=false;
        this.setState({ buttonLoading:false})
        break;
      }
    }
    params = {
      fkWorkflowId: this.props.processId,
      paramJsonString: JSON.stringify(this.props.paramsData),
      ...params
    }
    createExperiment(params).then(res=>{
      if(res){
        if(res.data.meta.code === '001'){
          message.success('')
          this.setState({visible: false})
          this.props.history.push('/index/process')
        }else{
          message.error(res.data.meta.message)
        }
      }
    })
  }

  handleCancel = () => {
    this.setState({visible: false})
  }

  componentDidMount(){
    let componentInfo = {}    //   
    loadPublicComponentInfo().then(res=>{
      if(res.data.meta.code == '001'){
        res.data.data['System Component'].map(item=>{
          componentInfo = { ...componentInfo, [item.componentId] : item.variables}
        })
        this.props.saveComponentInfo(componentInfo)
      }
    })
  }

  render() {
    const {getFieldDecorator}=this.props.form;
    const config={
        rules:[{
            required:true,
            message:'Please input!'
        }]
    }
    const { handleNodeClick, handleCanvasClick, rightSideVis, lock, currentNode } = this.props;

    return (<div className={styles.canvasContainer}>
        <GGEditor className={styles.editor}>
            <PageHeader
              className={styles.pageheader}
              title=""
              onBack={() => this.props.history.push('/index/process')}
              subTitle=""
              extra={[
                // <Button type="primary"></Button>, 
                <Button type="primary" onClick={()=>this.setState({visible: true})}></Button>,    
              ]}
          />
          <Row type="flex" className={styles.editorBd} style={{height:'100%'}}>
            <Col xxl={18} xl={19} lg={18} md={17} sm={16} xs={16} className={styles.editorContent}>
              <CustomNode />
              <StartNode />
              <Flow 
                className={styles.flow}
                // onNodeClick={(e)=>{handleNodeClick(e);}}
                // onClick={(e)=>{handleCanvasClick(rightSideVis, lock, currentNode);}}
                data={this.state.processObj}
                />
            </Col>
            <Col xxl={6} xl={5} lg={6} md={7} sm={8} xs={8} className={styles.editorSidebar}>
              <FlowDetailPanel/>
            </Col>
          </Row>
          {/* <FlowContextMenu /> */}
        </GGEditor>
      <Modal
          title=""
          visible={this.state.visible}
          onOk={this.handleSaveExp}
          onCancel={this.handleCancel}
          confirmLoading={this.state.buttonLoading}
        >
          <Form labelCol={{span:6}} wrapperCol={{span:12}}>
            <Form.Item label=''>
              {
                  getFieldDecorator('name',config)(<Input placeholder='' />)
              }
            </Form.Item>
            <Form.Item label=''>
              {
                  getFieldDecorator('experimentDesc',config)(<Input.TextArea placeholder='' />)
              }
            </Form.Item>
          </Form>
        </Modal>
   </div> );
  }
};

const mapStateToProps = (state) => {
    return {
      rightSideVis: state.editor.rightSideVis,
      lock: state.editor.lock,
      currentNode: state.editor.currentNode,
      processData: state.process.processData,
      processId: state.process.processId,
      paramsData: state.process.paramsData
    }
  }
  
const mapDispatchToProps = (dispatch) => {
    return {
      // handleNodeClick(e) {
      //   if(e.item) {
      //     let label = e.item.model.label;
      //     dispatch(actionCreator.showRightSide(label));
      //    } else {
      //     dispatch(actionCreator.showRightSideNoParam());
      //   }
      // },
      // handleCanvasClick(rightSideVis, lockStatus, currentNode) {
      //   if(lockStatus) {
      //     dispatch(actionCreator.showRightSide(currentNode.label));
      //   }else{
      //     dispatch(actionCreator.hideRightSide());
      //   }
      // },
      // lockDrawer() {
      //   dispatch(actionCreator.lockRightSide());
      // },
      // unlockDrawer() {
      //   dispatch(actionCreator.unlockRightSide());
      // },
      saveProcessId(processId){
        dispatch(actionCreators.saveProcessId(processId))
      },
      saveComponentInfo(componentInfo){
        dispatch(actionCreator.saveComponentInfo(componentInfo))
      }
    }
  }
  
export default connect(mapStateToProps, mapDispatchToProps)(CreateExperiment);

